import useSWR from 'swr';
import { Typography as T, Layout } from 'antd';

import './HouseView.scss';

const fetcher = url => fetch(url).then(r => r.json());

const HouseView = props => {

  const { houseId } = props.match.params;

  const { data, error } = useSWR(`${process.env.REACT_APP_API_URL}/items/house/${houseId}?fields=*,*.*`, fetcher);

  if (error) {
    return <HouseError />
  } else if (data) {
    return <HouseLoaded {...data.data } />
  } else {
    return <HouseLoading />
  }

}

const HouseLoading = () => {

  return (
    <div>Loading...</div>
  )

}

const HouseError = () => {

  return (
    <div>Error!</div>
  )

}

const HouseLoaded = props => {

  const imageIds = props.images.map(img => img.directus_files_id);

  const backgroundImage = imageIds.length > 0 ? `url(${process.env.REACT_APP_API_URL}/assets/${imageIds[0]}?width=1200&height=600)` : null; 

  return (
    <Layout>
      <header className="house-header" style={{ backgroundImage }}>
        <div className="header-info">
          <T.Title>{ props.house_name }</T.Title>          
          <T.Title level={2}>{ props.city }</T.Title>
          <T.Text className="key-properties">{ props.number_of_rooms } Rooms, { parseFloat(props.house_dimensions).toFixed() } mÂ²</T.Text>
        </div>
      </header>
    </Layout>
  )

}

export default HouseView;